@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.logo {
  width:180px;height:80px;background-image: url(/athfix-app-for-html/static/media/athfix.b60ecdf5.png);
 background-size:50%;background-repeat: no-repeat;
  text-align: right;font-size:35px;line-height:100px;
  font-family: 'Raleway', sans-serif;color:rgb(83, 108, 247);
}
.App{
  position: absolute;z-index:4;left:0px;right:0px;height:80px;
  background-color: rgb(252, 251, 251);border-top:2px solid rgb(50, 245, 76);
  box-shadow:2px 2px 9px rgb(165, 164, 164);top:0px;
}
#menu{
  position: absolute;right:20px;top:30px;
}
k{position: absolute;left:20px;margin-top:50px;color:black;font-family: "Raleway",sans-serif;color:rgb(50, 50, 202);opacity:0;transition: ease-in-out 0.4s;}
l{position: absolute;left:20px;margin-top: 40px;color:black;font-family: "Raleway",sans-serif;color:rgb(50, 50, 202);opacity: 0;transition: ease-in-out 0.4s;}
#menuBar{position:fixed;z-index:3;background-color: white;top:80px;left:-300px;bottom:0px;width:250px;transition:ease-in-out 0.9s;box-shadow: 1px 1px 10px lightgrey;overflow-y:hidden;}
#menuBar li{ margin-top:15px;border-bottom:1px solid rgb(133, 133, 133);font-size:23px;list-style-type: none;text-align: center;
     line-height:80px;font-family: "Raleway",sans-serif;color:black;}
a{text-decoration: none;color:black;cursor:pointer;}
#menuBar li:nth-child(7){
   width:70%;height:50px;border-bottom: none;
   margin-left:30px;margin-top:20px;border-radius: 25px;
   background-color: rgb(230, 230, 4);line-height:40px;color:white;
}
#menuBar li:nth-child(7):hover{
  background-color: rgb(163, 163, 4);cursor: pointer;
}
#menuBar li:hover{
  font-size:25px;background-color: rgb(219, 218, 218);
  cursor:pointer;
}
#men{
  margin-left:-30px;
}
#setting{
  position:fixed;right:5px;top:20%;list-style-type: none;text-align: center;
  /*width:80px*/width:0px;height:290px;border-radius: 10px;z-index:1;overflow: hidden;
}
#setting li{width:50px;height:50px;margin-top:30px;border-radius: 10px;margin-left:95px;/*left:14px;*/
        background-color: white;}
#setting li:nth-child(1){
   transition: ease-in-out 0.7s;color:white;background-color: rgb(10, 24, 73);line-height:40px;
}
#setting li:nth-child(2){
  transition: ease-in-out 0.9s;color:white;background-color: black;
}
#setting li:nth-child(3){
  transition: ease-in-out 1.1s;color:white;background-color: rgb(211, 71, 71);
}
#quiz{position: fixed;right:10px;list-style-type: none;/*width:100px*/width:0px;height:120px;
  text-align: center;top:25%;z-index:2;overflow: hidden;}
#quiz li{border-radius: 10px;height:40px;margin-top:10px;font-family: "Poppins",sans-serif;font-size:20px;
  color:rgb(255, 255, 255);background-color: rgb(72, 89, 240);margin-left:105px;}
  #quiz li:nth-child(1){transition: ease-in-out 0.9s; }
  #quiz li:nth-child(2){transition: ease-in-out 1.2s; }

#resource{/*width:80px*/width:0px;height:270px;position: fixed;right:10px;top:20%;z-index:2;overflow:hidden;}
#resource li{width:50px;height:50px;border-radius: 10px;margin-top:30px;margin-left:75px;/*margin-left:75px*/list-style-type: none;}
#resource li:nth-child(1){
  background-color: rgb(84, 233, 84);color:white;
  transition: ease-in-out 0.9s;
}
#resource li:nth-child(2){
  background-color: rgb(65, 68, 224);color:white;
  transition: ease-in-out 1.1s;
}
#resource li:nth-child(3){
  background-color: black;color:white;
  transition: ease-in-out 1.3s;
}
#BrekTime{
  position:fixed;top:25%;right:100px;width: 60px;height:0px;
 border-radius: 10px;z-index:2;transition: ease-in-out 0.4s;z-index:3;
  background:linear-gradient(#4568dc, #b06ab3);overflow: hidden;
}
#BrekTime li{list-style-type: none;text-align: center;margin-left:5px;
  border-radius: 10px;width:50px;height:50px;background-color:white;font-weight:bolder;
   margin-top:8px;line-height:40px;font-size:25px;font-family: "Poppins",sans-serif;}

#BrekTime li:nth-child(1){
  background-color:black;color:white;font-weight: bolder;
}
#alertMess{
  width:0px;height:0px;border-radius: 30px;overflow:hidden;
  box-shadow: 5px 5px 50px rgb(158, 157, 157);position: fixed;z-index:3;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);left:50%;top:50%;transition: ease-in-out 0.2s;
  background:linear-gradient(rgb(204, 204, 204) 15%,white 4%)
}
#red{
    width:20px;height:20px;border-radius:10px;background-color: rgb(245, 52, 52);
    position: absolute;right:20px;color:red;text-align:center;line-height:18px;
}
#red:hover{
  color:white;cursor:pointer;
}
#yellow:hover{
  color:black;cursor: pointer;
}
#green:hover{
  color:black;cursor: pointer;
}
#yellow{
  width:20px;height:20px;border-radius:10px;background-color: rgb(235, 235, 56);
  position: absolute;right:50px;color: rgb(235, 235, 56);text-align: center;line-height:20px;
}
#green{
  width:20px;height:20px;border-radius:10px;background-color: rgb(97, 248, 60);
  position: absolute;right:80px;color:rgb(97, 248, 60);text-align: center;line-height:20px;
}
o{
  position: absolute;left:30px;top:15px;font-size: 25px;
  font-family: "Raleway",sans-serif;
}
form{
    width:0%;height:0%;overflow: hidden;z-index:2;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);left:50%;top:50%;position: fixed;
    background:linear-gradient(lightgrey 8%, white 4%);border-radius: 30px;
    box-shadow: 4px 5px 20px lightgrey;

}
input {
    width:200px;height:40px;margin-top:20px;
    font-family: "Poppins",sans-serif;
    border-radius:15px;
}
label{
    font-family: "Poppins",sans-serif;
}
#cut{
  width:20px;height: 20px;border-radius: 13px;background-color:red;
  position: absolute;right:20px;text-align: center;line-height:18px;
  color:red;font-family: "Poppins",sans-serif;
}
#cut:hover,#exp:hover,#line:hover{
  color:black;cursor:pointer;
}
#exp{
  width:20px;height: 20px;border-radius: 13px;background-color:rgb(26, 236, 26);
  position: absolute;right:45px;text-align: center;line-height:18px;color:rgb(26, 236, 26);
}

#line{
  width:20px;height: 20px;border-radius: 13px;background-color:rgb(219, 243, 84);
  position: absolute;right:70px;text-align: center;line-height:18px;color:rgb(219, 243, 84);
}

#fill{
    -webkit-transform:translate(-50%,-90%);
            transform:translate(-50%,-90%);left:50%;top:90%;
    position: absolute;
}
#sub{
  border:none;
}
legend{
    font-family: "Poppins",sans-serif;
}
textarea{
    border:2px solid white;outline:none;
    font-family:"Poppins",sans-serif;
    
}
fieldset{
    margin-top:30px;border:none;border-radius: 30px;
    box-shadow: 3px 3px 20px lightgrey;
}
#sub{
 background-color: rgb(81, 81, 235);
    font-size: 25px;color:white;
}

@media (max-width:1290px){
  #ad{
    margin-top:60%;
  }
}
@media (max-width:1230px){
  #ad{
    margin-top:90%;
  }
}
